@import url("https://fonts.googleapis.com/css2?family=Manrope:wght@600;800&display=swap");
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Manrope", sans-serif;
  font-size: 1.6rem;
  text-align: center;
  background-image: url("/images/bg-pattern.svg");
  background-repeat: no-repeat;
  background-size: 100% 60%;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

.centar {
  display: flex;
  align-items: center;
  justify-content: center;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
  color: #303b61;
}

a:hover {
  color: #1d2647;
}

/** Media Queries **/
header {
  background-image: url("/images/pattern-circles.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 20rem;
  padding: 3rem;
}

.text {
  padding: 3rem 0;
}
.text h1 {
  color: #303b61;
  font-weight: 800;
  font-size: 2.5rem;
}
.text p {
  color: #8b8faa;
  font-size: 2rem;
  display: flex;
  flex-direction: column;
}
@media (min-width: 700px) {
  .text h1 {
    font-size: 3rem;
  }
  .text p {
    display: inline-block;
    font-size: 2rem;
  }
}

.tarjeta {
  background-color: #fff;
  border-radius: 2rem;
  padding: 2rem;
  box-shadow: 0rem 0rem 2rem -1rem #000;
  display: inline-block;
  margin: 2rem 0;
  font-weight: 600;
  color: #8b8faa;
}
@media (min-width: 700px) {
  .tarjeta {
    width: 60rem;
    padding: 2rem 4rem;
  }
}

.price {
  display: flex;
  justify-content: center;
  align-items: center;
}
.price h2 {
  font-weight: 800;
  font-size: 4rem;
  color: #303b61;
}
.price p {
  color: #8b8faa;
  font-size: 1.5rem;
  padding-left: 0.5rem;
}
@media (min-width: 700px) {
  .price h2 {
    font-size: 5 rem;
  }
  .price p {
    font-size: 2rem;
  }
}

.barra input[type=range] {
  width: 100%;
}

.barra-rango {
  -webkit-appearance: none;
  border-radius: 3rem;
  height: 1.4rem;
  transition: all 0s linear;
}

.barra-rango::-webkit-slider-thumb {
  -webkit-appearance: none;
  cursor: pointer;
  background-image: url("/images/icon-slider.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-color: #47d1c2;
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
  box-shadow: 0rem 1.2rem 3rem #47d1c2;
}
@media (min-width: 700px) {
  .barra-rango::-webkit-slider-thumb {
    width: 4.5rem;
    height: 4.5rem;
  }
}

.barra-rango::-webkit-slider-thumb:hover {
  background-color: #1aaa99;
}

.tipoSubscripcion {
  display: flex;
  justify-content: center;
  align-items: center;
}
.tipoSubscripcion .cambiar {
  display: inline-block;
  background-color: #8b8faa;
  border-radius: 3rem;
  width: 5rem;
  height: 2rem;
  margin: 0 1rem;
  cursor: pointer;
  padding: 0;
}
@media (max-width: 400px) {
  .tipoSubscripcion .cambiar {
    width: 7.3rem;
  }
}
@media (min-width: 700px) {
  .tipoSubscripcion {
    margin: 3.5rem 0;
  }
  .tipoSubscripcion label {
    font-size: 1.7rem;
  }
}

.cambiar input {
  cursor: pointer;
  opacity: 0;
}

.inputs {
  display: flex;
  justify-content: space-between;
  max-width: 90%;
}

.cambiar {
  position: relative;
}
.cambiar .deslizar {
  width: 1.5rem;
  height: 1.5rem;
  border-radius: 50%;
  background-color: #fff;
  position: absolute;
  top: 0.2rem;
  left: 0.4rem;
  transition: all 0.5s ease-in-out;
}

.tipoSubscripcion-descuento {
  display: none;
  padding-left: 1rem;
}
.tipoSubscripcion-descuento p {
  background-color: #ffede9;
  color: #e99d83;
  font-size: 1rem;
  font-weight: 600;
  padding: 0.6rem;
  border-radius: 2rem;
}
@media (min-width: 700px) {
  .tipoSubscripcion-descuento p {
    font-size: 1.2rem;
  }
}

.descuento2 {
  display: none;
}

@media (min-width: 700px) {
  .descuento2 {
    display: inline-block;
  }

  .descuento {
    display: none;
  }
}
.obtenido li {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}
.obtenido li img {
  width: 1.5rem;
}
.obtenido button {
  border-radius: 2rem;
  padding: 1rem 4rem;
  color: #fff;
  background-color: #303b61;
  cursor: pointer;
  text-align: center;
  border-color: #303b61;
}
.obtenido button:hover {
  background-color: #1d2647;
}
@media (min-width: 700px) {
  .obtenido {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .obtenido button {
    padding: 1.2rem 5.5rem;
    font-size: 1.5rem;
    height: 30%;
  }
  .obtenido li {
    display: grid;
    grid-template-columns: 15% 85%;
  }
  .obtenido li p {
    text-align: left;
  }
}

@media (min-width: 700px) {
  .datos {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
    margin-bottom: 2rem;
  }
  .datos .price {
    grid-column: 2/3;
  }
  .datos .barra {
    grid-row: 2/3;
    grid-column: 1/3;
  }
  .datos .views {
    font-size: 2rem;
  }
}

.mostrar {
  display: inline-block;
}

footer {
  padding: 1rem;
}